import { StatusBar } from 'expo-status-bar';
import React, { useState, useEffect, useCallback, useContext } from 'react';

//library
import { StyleSheet, Text, View, ScrollView, TouchableOpacity, Image, Picker, Dimensions, TextInput, FlatList } from 'react-native';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';


//const
const windowWidth = Dimensions.get('window').width;
const windowHeight = Dimensions.get('window').height;

//icons
import Graph from '../assets/icons/Graph';
import User from '../assets/icons/User';
import Category from '../assets/icons/Category';
import Tovar from '../assets/icons/Tovar';
import Zakaz from '../assets/icons/Zakaz';


export default function Tovars({ navigation, route }) {

  const { id } = route.params;

  const [data, setData] = useState([]);
  const [dats, setDats] = useState([]);


  const del = (id) => {
    fetch('https://api.dentaled.ru/shoper/delTov.php', {
        method: 'POST',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          id: id
        })
      }).then((response) => response.json())
        .then((responseJson) => {
          alert('Удалено');
          tovars();
        }).catch((error) => {
          console.error(error);
        });
  }


  const setTovar = () => {

    fetch('https://api.dentaled.ru/catalog/cst.php', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      }
    }).then((response) => response.json())
      .then((responseJson) => {
        setDats(responseJson);
      }).catch((error) => {
        console.error(error);
      });

    fetch('https://api.dentaled.ru/item.php', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        id: id,
      })
    }).then((response) => response.json())
      .then((responseJson) => {
        if (responseJson != null) {
          setData(responseJson);
        } else {
          console.log('Ошибка')
        }
      }).catch((error) => {
        console.error(error);
      });

  } 

  const [title, setTitle] = useState('');
  const [price, setPrice] = useState('');
  const [model, setModel] = useState('');
  const [ider, setIder] = useState('');
  const [about, setAbout] = useState(''); 
  const [image, setImage] = useState(''); 



  const updateTovar = () => {

    fetch('https://api.dentaled.ru/shoper/updateTov.php', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        title: title,
        price: price,
        model: model,
        ider: ider,
        about: about,
        id: id,
        image: image
      })
    }).then((response) => response.json())
      .then((responseJson) => {
        if (responseJson == 1) {
          navigation.navigate('Home');
        } else {
          alert(responseJson);
        }
        
      }).catch((error) => {
        console.error(error);
      });

  } 

  useEffect(() => {
    setTovar();
  }, []);

  return (
    <View style={styles.container}>
      <View style={styles.blockhain}>
        <View style={styles.menubar}>
          <View style={{marginTop: 70}}>
            <TouchableOpacity onPress={() => navigation.navigate('Home')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Graph />
              <Text style={{color: '#fff', marginLeft: 20}}>Главный экран</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('Users')}style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <User />
              <Text style={{color: '#fff', marginLeft: 20}}>Пользователи</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('Categorys')}style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Category />
              <Text style={{color: '#fff', marginLeft: 20}}>Категории</Text>
            </TouchableOpacity>
            <TouchableOpacity style={{padding: 10, marginTop: 10, borderRadius: 5, backgroundColor: '#ff0000', shadowColor: '#ff0000', shadowOpacity: 0.2, shadowRadius: 10, shadowOffset: {
              width: 5,
              height: 5
            },  width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Tovar />
              <Text style={{color: '#fff', marginLeft: 20}}>Товары</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('Order')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Zakaz /> 
              <Text style={{color: '#fff', marginLeft: 20}}>Заказы</Text>
            </TouchableOpacity>
          </View>
        </View>
        <View style={styles.content}>
          <View style={{width: windowWidth/1.25, backgroundColor: '#252525', height: 70, justifyContent: 'center'}}>
            <Text style={{color: '#ff0000', fontWeight: '600', fontSize: 18, marginLeft: 20}}>DENTALED</Text>
          </View>
          <ScrollView style={{width: windowWidth/1.27, backgroundColor: '#202020', height: windowHeight, padding: 20}}>
            <TouchableOpacity onPress={() => navigation.navigate('ShopItem')} style={{padding: 15, backgroundColor: '#ff0000', borderRadius: 4, width: '20%'}}>
              <Text style={{color: '#fff', fontSize: 16, textAlign: 'center'}}>Назад к товарам</Text>
            </TouchableOpacity>
            <View style={{marginTop: 40}}>
            <FlatList
              data={data}
              showsVerticalScrollIndicator={false} 
              renderItem={({ item }) => (
                <View>
                  <View>
                    <Text style={{color: '#fff', fontSize: 28, fontWeight: '700'}}>{item.title}</Text>
                  </View>
                  <View style={{marginTop: 10}}>
                    <Text style={{color: '#fff', fontSize: 14, opacity: 0.4, fontWeight: '400'}}>#{item.id}</Text>
                  </View>
                  <View style={{marginTop: 40}}>
                    <Text style={{color: '#fff', fontSize: 14, opacity: 0.4, fontWeight: '400', paddingBottom: 15}}>Ссылка на изображение</Text>
                    <Text style={{color: '#fff', fontSize: 14, fontWeight: '400'}}>{item.image}</Text>
                  </View>
                  <View style={{marginTop: 20}}>
                    <Text style={{color: '#fff', fontSize: 14, opacity: 0.4, fontWeight: '400', paddingBottom: 15}}>Цена</Text>
                    <Text style={{color: '#fff', fontSize: 14, fontWeight: '400'}}>{item.price}</Text>
                  </View>
                  <View style={{marginTop: 20}}>
                    <Text style={{color: '#fff', fontSize: 14, opacity: 0.4, fontWeight: '400', paddingBottom: 15}}>Модель</Text>
                    <Text style={{color: '#fff', fontSize: 14, fontWeight: '400'}}>{item.model}</Text>
                  </View>
                  <View style={{marginTop: 20}}>
                    <Text style={{color: '#fff', fontSize: 14, opacity: 0.4, fontWeight: '400', paddingBottom: 15}}>Описание</Text>
                    <Text style={{color: '#fff', fontSize: 14, fontWeight: '400'}}>{item.about}</Text>
                  </View>
                </View>
              )}
                keyExtractor={item => item.id}
              />
              <View style={{flexDirection: 'row', alignItems: 'flex-start', marginTop: 30}}>
                <View>
                  <View>
                    <TextInput
                      style={{color: '#fff', fontSize: 15, padding: 15, width: 700, backgroundColor: '#000', borderRadius: 5 }}
                      onChangeText={setImage}
                      placeholderTextColor="#d3d3d3" 
                      value={image}
                      placeholder="Ссылка на изображение"
                    />
                  </View>
                  <View style={{marginTop: 20}}>
                    <TextInput
                      style={{color: '#fff', fontSize: 15, padding: 15, width: 700, backgroundColor: '#000', borderRadius: 5 }}
                      onChangeText={text => setTitle(text)}
                      placeholderTextColor="#d3d3d3" 
                      value={title}
                      placeholder="Название товара"
                    />
                  </View>
                  <View style={{marginTop: 20}}>
                    <TextInput
                      style={{color: '#fff', fontSize: 15, padding: 15, width: 700, backgroundColor: '#000', borderRadius: 5 }}
                      onChangeText={setPrice}
                      placeholderTextColor="#d3d3d3" 
                      value={price}
                      placeholder="Цена"
                    />
                  </View>
                  <View style={{marginTop: 20}}>
                    <TextInput
                      style={{color: '#fff', fontSize: 15, padding: 15, width: 700, backgroundColor: '#000', borderRadius: 5 }}
                      onChangeText={setModel}
                      placeholderTextColor="#d3d3d3" 
                      value={model}
                      placeholder="Модель"
                    />
                  </View>
                  <View style={{marginTop: 20}}>
                    <View>
                      <Picker
                        selectedValue={ider}
                        style={{ width: 150, paddingTop: 10, paddingBottom: 10, paddingLeft: 20, paddingRight: 20, backgroundColor: '#292929', color: '#fff', borderWidth: 0, borderRadius: 5 }}
                        onValueChange={(itemValue, itemIndex) => {
                          setIder(itemValue); 
                        }}
                      >

                      {dats != null ?
                        dats.map((item, key)=>(
                            <Picker.Item label={item.title} value={item.id} key={key} />)
                        )
                      : (
                        <Picker.Item label="Загрузка" value="0" />
                      )}
                      </Picker>
                    </View>
                  </View>
                  <View style={{marginTop: 20}}>
                    <TextInput
                      style={{color: '#fff', fontSize: 15, padding: 15, width: 700, backgroundColor: '#000', borderRadius: 5 }}
                      onChangeText={setAbout}
                      placeholderTextColor="#d3d3d3" 
                      multiline = {true}
                      numberOfLines = {10}
                      value={about}
                      placeholder="Описание товара"
                    />
                  </View>
                  <View style={{marginTop: 30, flexDirection: 'row', alignItems: 'center' }}>
                    <TouchableOpacity onPress={() => updateTovar()} style={{padding: 15, backgroundColor: '#ff0000', borderRadius: 4}}>
                      <Text style={{color: '#fff', fontSize: 16, textAlign: 'center'}}>Изменить</Text>
                    </TouchableOpacity>
                    <TouchableOpacity onPress={() => del()} style={{padding: 15, marginLeft: 20, backgroundColor: '#000', borderRadius: 4}}>
                      <Text style={{color: '#fff', fontSize: 16, textAlign: 'center'}}>Удалить</Text>
                    </TouchableOpacity>
                  </View>
                </View>
              </View>
            </View>
          </ScrollView>
        </View>
      </View>
    </View>
  );
}


const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
  },
  blockhain: {
    flexDirection: 'row',
    alignItems: 'center'
  },
  menubar: {
    width: '20%',
    backgroundColor: '#252525',
    height: windowHeight,
    alignItems: 'center'
  }, 
  content: {
    width: '80%',
    backgroundColor: '#202020',
    height: windowHeight
  }
});