import { StatusBar } from 'expo-status-bar';
import React, { useState, useEffect, useCallback, useContext } from 'react';

//library
import { StyleSheet, Text, View, ScrollView, TouchableOpacity, Image, Picker, Dimensions, TextInput, FlatList } from 'react-native';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';


//const
const windowWidth = Dimensions.get('window').width;
const windowHeight = Dimensions.get('window').height;

//icons
import GraphActive from '../assets/icons/GraphActive';
import User from '../assets/icons/User';
import Category from '../assets/icons/Category';
import Tovar from '../assets/icons/Tovar';
import Zakaz from '../assets/icons/Zakaz';


export default function Redaction({ navigation, route }) {

  const { id } = route.params;

  const [fors, setFors] = useState(false);
  const [ds, setDs] = useState([]);

  const [t, setT] = useState('');
  const [im, setIm] = useState('');
  const [ab, setAb] = useState('');

  const softs = () => {
    fetch('https://api.dentaled.ru/home/updatehomecat.php', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        id: id,
      })
    }).then((response) => response.json())
      .then((responseJson) => {
        setDs(responseJson);
      }).catch((error) => {
        console.error(error);
      });
  }

  const forever = () => {
    fetch('https://api.dentaled.ru/home/updatehomeit.php', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        id: id,
        title: t,
        image: im,
        about: ab
      })
    }).then((response) => response.json())
      .then((responseJson) => {
        navigation.navigate('Home');
      }).catch((error) => {
        console.error(error);
      });
  }


  useEffect(() => {
    softs();
  }, []);

  return (
    <View style={styles.container}>
      <View style={styles.blockhain}>
        <View style={styles.menubar}>
          <View style={{marginTop: 70}}>
            <TouchableOpacity style={{padding: 10, marginTop: 10, borderRadius: 5, backgroundColor: '#ff0000', shadowColor: '#ff0000', shadowOpacity: 0.2, shadowRadius: 10, shadowOffset: {
              width: 5,
              height: 5
            },  width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <GraphActive />
              <Text style={{color: '#fff', marginLeft: 20}}>Главный экран</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('Users')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <User />
              <Text style={{color: '#fff', marginLeft: 20}}>Пользователи</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('Categorys')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Category />
              <Text style={{color: '#fff', marginLeft: 20}}>Категории</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('ShopItem')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Tovar />
              <Text style={{color: '#fff', marginLeft: 20}}>Товары</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('Order')} style={{padding: 10, marginTop: 10, borderRadius: 5, width: 300, flexDirection: 'row', alignItems: 'center'}}>
              <Zakaz /> 
              <Text style={{color: '#fff', marginLeft: 20}}>Заказы</Text>
            </TouchableOpacity>
          </View>
        </View>
        <View style={styles.content}>
          <View style={{width: windowWidth/1.25, backgroundColor: '#252525', height: 70, justifyContent: 'center'}}>
            <Text style={{color: '#ff0000', fontWeight: '600', fontSize: 18, marginLeft: 20}}>DENTALED</Text>
          </View>
          <ScrollView style={{width: windowWidth/1.27, backgroundColor: '#202020', height: windowHeight, padding: 20}}>
            <View style={{marginTop: 30}}>
              <Text style={{color: '#fff', fontSize: 16}}>Редактирование акции</Text>
                <View style={{marginTop: 20}}>
                  <View>
                    <TextInput
                      style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5}}
                      onChangeText={setT}
                      placeholderTextColor="#d3d3d3" 
                      value={t}
                      placeholder="Название товара"
                    />
                  </View>
                </View>
                <View style={{marginTop: 20}}>
                  <View>
                    <TextInput
                      style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5}}
                      placeholderTextColor="#d3d3d3"
                      onChangeText={setIm} 
                      value={im}
                      placeholder="Ссылка на баннер"
                    />
                  </View>
                </View>
                <View style={{marginTop: 20}}>
                  <View>
                    <TextInput
                      style={{color: '#fff', fontSize: 15, padding: 15, backgroundColor: '#000', borderRadius: 5}}
                      onChangeText={setAb}
                      multiline = {true}
                      numberOfLines = {10}
                      placeholderTextColor="#d3d3d3" 
                      value={ab}
                      placeholder="Описание"
                    />
                  </View>
                </View>
            </View>
              <View style={{padding: 20}}>
                <View style={{flexDirection: 'row', alignItems: 'center', justifyContent: 'flex-end', paddingRight: 20, paddingBottom: 50, marginTop: 30}}>
                  <TouchableOpacity onPress={() => forever()}  style={{paddingLeft: 30, paddingRight: 30, paddingTop: 10, paddingBottom: 10, backgroundColor: '#ff0000', borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                      <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Изменить</Text>
                  </TouchableOpacity>
                  <TouchableOpacity onPress={() => navigation.navigate('Home')}  style={{padding: 10, marginLeft: 20, borderRadius: 5, justifyContent: 'center', alignItems: 'center'}}>
                      <Text style={{color: '#fff', fontSize: 14, fontWeight: '500'}}>Отмена</Text>
                  </TouchableOpacity>
                </View>
              </View>
          </ScrollView>
        </View>
      </View>
    </View>
  );
}


const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
  },
  blockhain: {
    flexDirection: 'row',
    alignItems: 'center'
  },
  menubar: {
    width: '20%',
    backgroundColor: '#252525',
    height: windowHeight,
    alignItems: 'center'
  }, 
  content: {
    width: '80%',
    backgroundColor: '#202020',
    height: windowHeight
  }
});